<template>
  <div>
    <div class="filter-container">
      <el-row :gutter="10">
        <el-col :sm="24" :md="12">
          <el-button class="filter-item" @click="handleCreate" type="primary" icon="el-icon-plus">{{$t('table.add')}}</el-button>
          <el-button class="filter-item" type="danger" :disabled="multipleSelection.length <= 0"
            v-if="multipleSelection.length>0"
            @click="handleDelete(multipleSelection)"
            icon="el-icon-delete">{{$t('table.delete')}} ({{multipleSelection.length}} item)</el-button>
        </el-col>
        <el-col :sm="24" :md="12">
          <el-input placeholder="Search" v-model="urlParams.s" class="input-with-select" @keyup.enter.native="getShops">
            <el-button slot="append" icon="el-icon-search" @click="getShops"></el-button>
          </el-input>
        </el-col>
      </el-row>
    </div>

    <div class="table-container">
      <el-table :data="shops" v-loading="loading" element-loading-text="Memuat data..." @selection-change="handleSelectionChange" border fit highlight-current-row style="width: 100%">
        <el-table-column type="selection" width="55" style="margin:0 auto;"></el-table-column>

        <el-table-column prop="name" label="Name" sortable>
          <template slot-scope="scope">
            <router-link :to="{ name: 'shopDetail', params: { id: scope.row.shop_id }}">
              <a>{{scope.row.name}}</a>
            </router-link>
          </template>
        </el-table-column>
        <el-table-column prop="phone" label="Phone">
        </el-table-column>
        <el-table-column prop="address" label="Address" sortable>
        </el-table-column>

        <el-table-column align="center" :label="$t('table.actions')" width="230" class-name="small-padding fixed-width">
          <template slot-scope="scope">
            <el-button type="primary" size="mini" @click="handleUpdate(scope.row)" icon="el-icon-edit">{{$t('table.edit')}}</el-button>
            <el-button type="danger" size="mini" @click="handleDelete([scope.row])" icon="el-icon-delete" style="width:80px;" >{{$t('table.delete')}}</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- Pagination -->
      <div class="pagination">
        <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.currentPage"
          :page-sizes="pagination.sizes"
          :page-size="pagination.pageSize"
          layout="sizes, total, prev, pager, next"
          :total="pagination.total">
        </el-pagination>
      </div>
    </div>

    <el-dialog :title="textMap[formDialog.status]" :visible.sync="formDialog.visible" :width="formDialog.dialog.width">
      <el-form :model="shopForm" :rules="shopFormRules" ref="shopForm" :label-position="formDialog.dialog.labelPosition" label-width="100px">

        <el-form-item label="Name" prop="name">
          <el-input type="text" v-model="shopForm.name" placeholder="name"></el-input>
        </el-form-item>

        <el-form-item label="Phone" prop="phone">
          <el-input type="text" v-model="shopForm.phone" placeholder="phone"></el-input>
        </el-form-item>

        <el-form-item label="Address" prop="address">
          <el-input type="text" v-model="shopForm.address" placeholder="address"></el-input>
        </el-form-item>

        <el-form-item label="Users">
          <el-select v-model="shopForm.user_ids" multiple placeholder="Select" style="width:100%">
            <el-option
              v-for="item in users"
              :key="item.user_id"
              :label="item.name"
              :value="item.user_id">
            </el-option>
          </el-select>
        </el-form-item>

        <!-- Add products -->
        <div class="sub-title">
          <h3>Products</h3>
        </div>

        <div class="form-limited">
          <el-form-item
            v-for="(product, index) in shopForm.products"
            :label="'Product ' + (index+1)"
            :key="'product-'+index"
            :prop="'products.' + index + '.product'"
            >
            <el-row :gutter="5">
              <el-col :sm="8">
                <el-select
                  style="width:100%"
                  v-model="product.product_id"
                  filterable
                  remote
                  placeholder="Search product"
                  :remote-method="getProducts"
                  :loading="loading">
                  <el-option
                    v-for="item in products"
                    :key="item.product_id"
                    :label="item.name"
                    :value="item.product_id">
                  </el-option>
                </el-select>
              </el-col>
              <el-col :sm="6">
                <el-input type="number" v-model.number="product.stock" min="0" placeholder="stock"></el-input>
              </el-col>
              <el-col :sm="2">
                <el-button type="danger" icon="el-icon-delete" @click.prevent="removeProduct(product)"></el-button>
              </el-col>
            </el-row>
          </el-form-item>
        </div>

        <el-form-item>
          <el-button style="margin-left:10px" size="mini" type="primary" icon="el-icon-plus" @click="addNewProduct">Add Product</el-button>
        </el-form-item>

        <el-form-item>
          <el-button @click="formDialog.visible = false">{{$t('table.cancel')}}</el-button>
          <el-button v-if="formDialog.status==='create'" :loading="loading" type="primary" @click="createData">{{$t('table.confirm')}}</el-button>
          <el-button v-else type="primary" @click="updateData">{{$t('table.confirm')}}</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="Delete Confirmation"
      :visible.sync="deleteDialog.visible"
      :width="formDialog.dialog.width"
      center>
      <span>Are you sure want to delete?</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deleteDialog.visible = false">Cancel</el-button>
        <el-button type="primary" @click="deleteData">Yes</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
import { dialog } from "@/responsive";
import { arrayToObject } from "@/utils/arrayToObject";

const formFields = [
  "name",
  "phone",
  "addresss",
  ["user_ids", []],
  ["products", []]
];

export default {
  data() {
    return {
      shops: [],
      products: [],
      users: [],
      loading: false,
      textMap: {
        update: "Edit",
        create: "Create"
      },

      // form
      formFields,
      shopForm: arrayToObject(formFields),
      shopFormRules: {
        name: [{ required: true }],
        phone: [{ required: true }],
        address: [{ required: true }]
      },

      // edit
      formDialog: {
        visible: false,
        status: "",
        selectedId: undefined,
        dialog: dialog.md
      },

      // delete
      deleteDialog: {
        visible: false,
        dialog: dialog.md30
      },
      deleteShopIds: [],

      // table
      multipleSelection: [],

      // Pagination
      pagination: {
        total: 0,
        pageSize: 10,
        currentPage: 1,
        sizes: [5, 10, 20, 30, 40, 50, 100]
      },

      // Get
      urlParams: {
        s: "",
        l: 10,
        p: 1,
        oby: "created_at", // order_by (shop_id|name|address|phone|created_at)
        sort: "desc"
      },

      // Responsive
      windowWidth: window.innerWidth
    };
  },

  methods: {
    // Create
    handleCreate() {
      this.resetForm();
      this.formDialog = {
        ...this.formDialog,
        status: "create",
        visible: true
      };
      this.$nextTick(() => {
        this.$refs["shopForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    createData() {
      this.$refs.shopForm.validate(valid => {
        if (valid) {
          this.loading = true;

          this.$store
            .dispatch("AddShop", this.shopForm)
            .then(res => {
              // refetch shops data
              this.getShops();

              this.$message.success(res.data.messages[0]);
              this.formDialog.visible = false;
              this.loading = false;
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Update
    handleUpdate(row) {
      // this.shopForm = Object.assign({}, row); // copy obj
      this.shopForm = {
        name: row.name,
        phone: row.phone,
        address: row.address,
        products: row.products,
        user_ids: row.users.map(user => {
          return user.user_id;
        })
      };

      console.log("row, ", row);
      console.log("user_ids, ", this.shopForm);
      this.formDialog = {
        status: "update",
        visible: true,
        selectedId: row.shop_id
      };
      this.$nextTick(() => {
        this.$refs["shopForm"].clearValidate();
      });

      // Responsive
      this.makeDialogResponsive();
    },
    updateData() {
      this.$refs.shopForm.validate(valid => {
        if (valid) {
          this.loading = true;

          // add shop Id
          this.shopForm.shopId = this.formDialog.selectedId;

          this.$store
            .dispatch("UpdateShop", this.shopForm)
            .then(res => {
              // refetch shops data
              this.getShops();

              this.loading = false;
              this.formDialog.visible = false;
              this.$message.success(res.data.messages[0]);
            })
            .catch(error => {
              const errorMsg = error.response.data.messages;
              errorMsg.forEach(msg => {
                this.$message.error(msg);
              });
              this.loading = false;
            });
        }
      });
    },

    // Delete
    handleDelete(shops) {
      this.deleteDialog.visible = true;
      this.deleteShopIds = shops.map(shop => {
        return shop.shop_id;
      });

      // Responsive
      this.makeDialogResponsive(dialog.sm, dialog.md30);
    },
    deleteData() {
      this.deleteDialog.visible = false;
      this.$store
        .dispatch("DeleteShop", this.deleteShopIds)
        .then(res => {
          // refetch shops data
          this.getShops();

          this.$message.success(res.data.messages[0]);
          this.deleteDialog.visible = false;
          this.deleteShopIds = [];
        })
        .catch(error => {
          const errorMsg = error.response.data.messages;
          errorMsg.forEach(msg => {
            this.$message.error(msg);
          });
        });
    },

    // Form
    resetForm() {
      // this.$refs[formName].resetFields();
      this.shopForm = arrayToObject(this.formFields);
    },
    addNewProduct() {
      this.shopForm.products.push({
        product_id: null,
        stock: null
      });
    },
    removeProduct(item) {
      var index = this.shopForm.products.indexOf(item);
      if (index !== -1) {
        this.shopForm.products.splice(index, 1);
      }
    },

    // Table
    handleSelectionChange(shops) {
      this.multipleSelection = shops;
    },

    // Pagination
    handleSizeChange(pageSize) {
      this.pagination = {
        ...this.pagination,
        pageSize
      };
      this.urlParams = {
        ...this.urlParams,
        l: pageSize
      };

      this.getShops();
    },
    handleCurrentChange(currentPage) {
      this.pagination = {
        ...this.pagination,
        currentPage
      };
      this.urlParams = {
        ...this.urlParams,
        p: currentPage
      };
      this.getShops();
    },

    // Get
    getShops() {
      this.loading = true;
      this.$store.dispatch("FetchShops", this.urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.shops = data.data.shops;
        this.loading = false;

        // set pagination
        this.pagination = {
          ...this.pagination,
          total: data.data.total
        };
      });
    },
    getProducts(query) {
      const urlParams = {
        s: query,
        l: 10,
        p: 1,
        oby: "created_at", // order_by (supplier_id|name|created_at)
        sort: "desc"
      };

      this.loading = true;
      this.$store.dispatch("FetchProducts", urlParams).then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.products = data.data.products;
        this.loading = false;
      });
    },
    getUsers() {
      this.loading = true;
      this.$store.dispatch("FetchUsers").then(res => {
        const data = res.data;
        const messages = data.messages;
        const status_code = data.status_code;

        this.users = data.data.users;
        this.loading = false;
      });
    },

    // Responsive
    makeDialogResponsive(sm = dialog.sm, md = dialog.md) {
      if (this.windowWidth < 990) {
        this.formDialog = {
          ...this.formDialog,
          dialog: sm
        };
      } else {
        this.formDialog = {
          ...this.formDialog,
          dialog: md
        };
      }
    }
  },

  watch: {
    windowWidth(newWidth, oldWidth) {
      this.makeDialogResponsive();
    }
  },

  created() {
    this.getShops();
    this.getUsers();
  },

  mounted() {
    let that = this;
    this.$nextTick(function() {
      window.addEventListener("resize", function(e) {
        that.windowWidth = window.innerWidth;
      });
    });
  }
};
</script>

<style scoped>

</style>



